<template>
    <div class="navBar">
        <fileMenu v-bind:files="onServerFiles" @loadNewFile="loadNewFile" @loadProjectFile="loadProjectFile" />
    </div>
    <div class="visual">
        <visual></visual>
    </div>
</template>

<script>
import fileMenu from "@/components/fileMenu.vue"
import axios from 'axios';
export default{
    components:{
        fileMenu
    },
    data(){
        return{
            onServerFiles:[],
            anatomicalData:null,
        }
    },
    methods:{
        async loadProjectList(){
            try{
                const response = await axios.get('http://192.168.1.27:5000/listProjects')
                this.onServerFiles = response.data['list']
            }catch(e){
                alert('Не удалось получить список проектов')
            }
        },
        loadNewFile(file){
            const formData = new FormData();
            formData.append('anatomical',file)
            const hdr = {
                headers: {
                "Content-Type": 'multipart/form-data',
                "projectName":"cringyCat2"
                },
            };

            let url = 'http://192.168.1.27:5000/newProject';
            return axios.post(url, formData, hdr).then((res) => ({
                status: res.status,
                data: res.data,
                error: null,
                }))
                .catch((err) => {
                    return {
                        status: err.response ? err.response.status : 0,
                        data: {},
                        error: err.message,
                    };
                })
        },
        async loadProjectFile(name){

            try{
                let headers = {
                    headers:{
                    "Content-Type": 'text/cmd',
                    "project":name
                    }
                }
                const response = await axios.get('http://192.168.1.27:5000/loadProject',headers)
                let base64Anatomical = response.data['anatomicalData']
                console.log(base64Anatomical)
                this.anatomicalData = "data:image/png;base64, "+base64Anatomical;
            }catch(e){
                console.log("Error: "+e)
            }
        }
    },
    mounted(){
        this.loadProjectList()
    }
}
</script>


